.switch {
  position: relative;
}

.switch .control-input {
  position: absolute;
  z-index: -1;
  opacity: 0;
}

.switch .control-label {
  position: relative;
  padding-left: calc(1.75rem + 4px);
}

.switch .control-label::before {
  content: "";
  display: block;
  position: absolute;
  top: 0.25rem;
  left: 0rem;
  width: 1.75rem;
  height: 1rem;
  border-radius: 50rem;
  pointer-events: all;
  background-color: #448aff;
  border: #448aff solid 1px;
  transition: background-color 0.2s ease-in-out, border-color 0.2s ease-in-out, box-shadow 0.2s ease-in-out;
}

.switch .control-label::after {
  content: "";
  display: block;
  position: absolute;
  top: calc(0.25rem + 2px);
  left: 14px;
  width: calc(1rem - calc(2px * 2));
  height: calc(1rem - calc(2px * 2));
  background-color: #fff;
  border-radius: 50rem;
  transition: transform 0.2s ease-in-out, background-color 0.2s ease-in-out, border-color 0.2s ease-in-out, box-shadow 0.2s ease-in-out;
}

.switch .control-input:checked ~ .control-label::before {
  border-color: #adb5bd;
  background-color: #fff;
}

.switch .control-input:checked ~ .control-label::after {
  background-color: #adb5bd;
  transform: translateX(-0.75rem);
}

.alerts {
  padding-right: 15px;
  padding-left: 15px;
}

.alerts .wrap {
  padding-right: 15px;
  padding-left: 15px;
  color: #454F5B !important;
  background-color: #fff !important;
  flex-direction: row !important;
  justify-content: flex-end !important;
  align-items: center !important;
  padding-bottom: 0.5rem !important;
  padding-top: 0.5rem !important;
  -webkit-animation: moveBox 1s ease-out;
          animation: moveBox 1s ease-out;
  -webkit-animation-iteration-count: 8;
          animation-iteration-count: 8;
  position: relative;
}

.alerts .shadow {
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
}

.alerts .card {
  position: relative;
  display: flex !important;
  background-clip: border-box;
  border: 1px solid rgba(0, 0, 0, 0.125);
  border-radius: .25rem;
}

.alerts .dir {
  width: 12px;
  height: 12px;
  border-top: 1px solid rgba(0, 0, 0, 0.125);
  border-right: 1px solid rgba(0, 0, 0, 0.125);
  background: #ffffff;
  transform: rotate(45deg);
  position: absolute;
  top: 50%;
  right: -6px;
  margin-top: -6px;
}

@-webkit-keyframes moveBox {
  form {
    transform: translateX(0px);
  }
  to {
    transform: translateX(24px);
  }
}

@keyframes moveBox {
  form {
    transform: translateX(0px);
  }
  to {
    transform: translateX(24px);
  }
}

